export class WsSendUdpBox {
  //  收件人名称
  recipientName?: string;
  // 发件人名称
  senderName: string;
  //具体的一个操作类型
  methodType?: number;
  //  数据信息
  body?: any;
  // 数据格式类型
  bodyType?: any;
  // 中介服务器 ip
  serverUdpProxy?: string;
  // 构造函数
  constructor(senderName: string) {
    this.senderName = senderName
  }
  toJSONObj(): string {
    return "JSON.stringify(this)"
  }
  // 进行用户注册
  static regisUserName(senderName: string, serverUdpProxy: string): string {
    let obj = new WsSendUdpBox(senderName)
    obj.methodType = 5
    obj.serverUdpProxy = serverUdpProxy
    return JSON.stringify(obj)
  }
  // 进行数据发送
  static sendMessage(senderName: string, recipientName: string, body: any): string {
    let obj = new WsSendUdpBox(senderName)
    obj.recipientName = recipientName
    obj.methodType = 6
    obj.bodyType = "string"
    obj.body = body
    return JSON.stringify(obj)
  }
  // 进行数据定义具体的method
  static sendMessageByType(senderName: string, recipientName: string, methodType: number, body: any): string {
    let obj = new WsSendUdpBox(senderName)
    obj.recipientName = recipientName
    obj.methodType = methodType
    obj.bodyType = "string"
    obj.body = body
    return JSON.stringify(obj)
  }
}

export interface SendWsBox {
  resCode: number
  errorMsg?: string
  body?: any
}
export function scrollToBottom(id: string): void {
  let div = document.getElementById(id);
  if (div) {
    console.log(div?.scrollHeight + 100);

    div.scrollTop = div?.scrollHeight + 900;
  }
}
