
import { IconUser, IconDriveFile } from "@arco-design/web-vue/es/icon";

import { defineComponent, ref, reactive, Ref, watch, nextTick, onMounted } from "vue";
import { Notification } from "@arco-design/web-vue";
import { WsSendUdpBox, SendWsBox, scrollToBottom } from "@/views/ws";
import { hasFile, accpectFileBody, fileCompute } from "./homeApi/api";
import Heads from "../components/Heads.vue";
const regexp = new RegExp(
  /^[a-zA-Z]:\\(?:[\u4e00-\u9fa5_a-zA-Z0-9]+\\)*[\u4e00-\u9fa5_a-zA-Z0-9]+\.\w+$/
);
export default defineComponent({
  name: "Home",
  components: { Heads, IconUser, IconDriveFile },
  setup() {
    const filePath = reactive({
      filePath: "",
    });
    // 接收到文件进行数据提醒
    const accpectFilevisible = ref(false);
    const message = ref("");
    // 聊天人
    const tragetUserName = ref("");
    // 文件上传提示框
    const filevisible = ref(false);
    // 用户列表抽屉
    const userVis = ref(false);
    const userName = ref("");
    const userList: Ref<string[]> = ref([]);
    // 连接输入弹窗
    const visible = ref(true);
    // form数据的绑定
    const form = reactive({
      userName: "",
      proxyUrl: "",
    });
    // 定义一个websocket对象
    var socket: WebSocket | null = null;
    // 是否接收文件-确定
    const accpectFileOk = () => {
      if (accpectFileBody.value.senderName) {
        socket?.send(
          WsSendUdpBox.sendMessageByType(
            userName.value,
            accpectFileBody.value.senderName,
            8,
            JSON.stringify(accpectFileBody.value)
          )
        );
      }

      accpectFilevisible.value = false;
    };
    // 是否接收文件-取消
    const accpectFilecancel = () => {
      accpectFilevisible.value = false;
    };
    // 聊天通知数据
    const accpectFileBody: Ref<accpectFileBody> = ref({});
    const records = ref([
      {
        type: 2,
        time: new Date().toDateString(),
        name: "系统提示",
        content: "请选中联系人，进行聊天",
      },
    ]);
    watch(records.value, async () => {
      await nextTick();
      scrollToBottom("pzpMain");
    });

    // 连接服务器的方法
    const connectServer = function (
      userName: string,
      wsUrl: string,
      proxyUrl: string,
      done: (closed: boolean) => void
    ) {
      socket = new WebSocket(wsUrl);
      socket.onopen = function () {
        socket?.send(WsSendUdpBox.regisUserName(form.userName, form.proxyUrl));
      };
      socket.onmessage = function (evt: MessageEvent<string>) {
        if (!evt.data) {
          return;
        }
        let resBody: SendWsBox = JSON.parse(evt.data);
        if (resBody.resCode != undefined || resBody.resCode != null) {
          if (resBody.resCode == 0) {
            Notification.error(resBody.errorMsg ? resBody.errorMsg : "发送信息错误");
            done(false);
          }
          if (resBody.resCode == 9) {
            Notification.success("连接服务器成功。。");
            done(true);
          }
          switch (resBody.resCode) {
            // 用户列表发送
            case 4:
              userList.value = resBody.body;
              break;
            // 同时用户是否进行文件的接收
            case 7:
              accpectFileBody.value = resBody.body as accpectFileBody;
              accpectFilevisible.value = true;
              break;
            // 接收用户发送的消息
            case 8:
              let receiveMessage = resBody.body;
              records.value.push({
                type: 2,
                time: new Date().toDateString(),
                name: receiveMessage.senderName,
                content: receiveMessage.message,
              });
              break;
            case 11:
              Notification.info(resBody.body);
              break;
          }
        } else {
          Notification.error(JSON.stringify(resBody));
        }
      };
      socket.onclose = function () {
        Notification.error("服务器关闭");
      };
    };
    // 发送消息
    const sendMessage = () => {
      if (tragetUserName.value) {
        if (!message.value) {
          Notification.error("发送内容不可以为空！");
          return;
        }
        records.value.push({
          type: 1,
          time: new Date().toDateString(),
          name: userName.value,
          content: message.value,
        });

        socket?.send(
          WsSendUdpBox.sendMessage(userName.value, tragetUserName.value, message.value)
        );
        message.value = "";
      } else {
        Notification.error("请选择发送人！");
        userVis.value = true;
      }
    };
    // 文件上传前置校验
    const handleFilePathOk = (done: (closed: boolean) => void) => {
      if (!filePath.filePath) {
        Notification.error("请输入具体文件地址路径！");
        done(false);
        return;
      } else {
        if (!regexp.test(filePath.filePath)) {
          Notification.error("输入文件路径有误请重新输入！");
          done(false);
          return;
        }
      }

      if (!tragetUserName.value) {
        Notification.error("请选择发送人！");
        done(false);
        return;
      }
      // 判断是否有对应的文件
      hasFile(filePath.filePath)
        .then((v) => {
          if (v.data.res) {
            Notification.success("找到文件,进行数据发送");
            socket?.send(
              WsSendUdpBox.sendMessageByType(
                userName.value,
                tragetUserName.value,
                7,
                filePath.filePath
              )
            );
            done(true);
          } else {
            Notification.error("未找到文件！");
            done(false);
            return;
          }
        })
        .catch(() => {
          Notification.error("系统错误");
          done(false);
          return;
        });
    };

    const handleBeforeOk = (done: (closed: boolean) => void) => {
      userName.value = form.userName;
      const input: HTMLInputElement | null = document.getElementById(
        "demo1"
      ) as HTMLInputElement | null;
      if (input) {
        connectServer(
          form.userName,
          "ws://127.0.0.1:" + input.value + "/ssssss",
          form.proxyUrl,
          done
        );
      }
    };
    const handleCancel = () => {
      userVis.value = false;
    };
    const changeUserName = (tName: string) => {
      if (userName.value == tName) {
        Notification.error("无法选择本人进行聊天");
      } else {
        tragetUserName.value = tName;
        userVis.value = false;
      }
    };
    const realHeight = ref(600);
    onMounted(() => {
      realHeight.value = window.innerHeight;
    });
    return {
      realHeight,
      changeUserName,
      userVis,
      fileCompute,
      handleCancel,
      userName,
      visible,
      filevisible,
      form,
      handleBeforeOk,
      records,
      filePath,
      userList,
      accpectFileOk,
      accpectFilecancel,
      handleFilePathOk,
      message,
      accpectFileBody,
      accpectFilevisible,
      sendMessage,
    };
  },
});
