import axios, { AxiosResponse } from "axios";
export interface restBoolean {
  res: boolean
}
export interface accpectFileBody {
  fileHashCode?: number,
  fileLength?: number,
  fileName?: string,
  recipientName?: string,
  senderName?: string
}
const url = '/api/hasFile?filePath='
export function hasFile(filePath: string): Promise<AxiosResponse<restBoolean>> {
  return axios.get(url + filePath)
}
/**
 * 进行转换
 * @param size 文件大小kb
 * @returns
 */
export function fileCompute(size: number | undefined): string {
  if (!size) {
    return ""
  }
  if (size < 1024) {
    return `${size}Byte`
  }
  if (size > 1024 && size < 1048576) {
    return `${(size / 1024).toFixed(2)}Kb`
  }
  if (size > 1048576 && size < 1073741824) {
    return `${(size / 1048576).toFixed(2)}Mb`
  }
  return `${(size / 1073741824).toFixed(2)}Gb`
}
